import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {filterAriaAttributes} from '@servicenow/library-enhanced-utils';
import styles from './_now-toggle.scss';
import focusBehavior from '@servicenow/behavior-focus';
import {configAriaSchema} from '@servicenow/library-schemas';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const view = (state, {dispatch}) => {
	const {checked, disabled, size, configAria} = state.properties;

	const onToggleClicked = () => {
		if (!disabled) {
			dispatch(({properties}) => {
				return {
					type: 'NOW_TOGGLE#CHECKED_SET',
					payload: {value: !properties.checked}
				};
			});
		}
	};

	return (
		<input
			type="checkbox"
			role="switch"
			{...filterAriaAttributes(configAria, 'switch')}
			checked={checked}
			aria-checked={String(checked)}
			disabled={disabled}
			on-click={onToggleClicked}
			class={{
				'now-toggle': true,
				'is-checked': checked,
				'is-disabled': disabled,
				['-' + size]: size
			}}
		/>
	);
};

/**
 * A toggle is a switch that gives the user the ability to change a state on or off.
 * This component acts like a button in that it shows the result of a user's action.
 *
 * ```jsx
 * <now-toggle checked></now-toggle>
 * <now-toggle disabled></now-toggle>
 * ```
 *
 * @seismicElement now-toggle
 * @summary The toggle component changes the state of a single setting to on or off.
 * @uib.label Toggle
 * @uib.icon toggle
 * @uib.description The toggle component changes the state of a single setting to on or off.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties size, checked, disabled, configAria
 * @uib.actions NOW_TOGGLE#CHECKED_SET
 */
createEnhancedElement('now-toggle', {
	properties: {
		/**
		 * When true, the toggle is selected. Use `manage-checked` to override
		 * this behavior and handle the `NOW_TOGGLE#CHECKED_SET` action manually.
		 * @type {boolean}
		 * @uib.description Indicates the checked state of the toggle
		 */
		checked: {
			reflect: true,
			default: false,
			manageable: true,
			schema: {type: 'boolean'}
		},
		/**
		 * When true, the toggle is disabled.
		 * @type {boolean}
		 * @uib.description Select to disable the toggle. 
		 * @uib.defaultValue false
		 */
		disabled: {reflect: true, default: false, schema: {type: 'boolean'}},
		/**
		 * Size of the toggle.
		 * @type {('sm'|'md')}
		 */
		size: {default: 'md', schema: {type: 'string', enum: ['sm', 'md']}},
		/**
		 * An object whose keys reference a specific HTML element within the
		 * now-toggle. The value of each key is an object that contains an ARIA
		 * property (or properties) and corresponding value/s. These ARIA
		 * properties are set on the specified element.
		 * The 'switch' key corresponds to the `<button>` with an ARIA role of
		 * 'switch' in now-toggle. See https://www.w3.org/TR/wai-aria-1.1/#switch for
		 * properties and accepted values.
		 * @type {{ 'aria-*': string }}
		 * @uib.label ARIA properties
		 * @uib.defaultValue {"aria-label": "Accessible Label"}
		 * @uib.description Configure ARIA properties
		 */
		configAria: {
			schema: {
				oneOf: [
					{
						type: 'object',
						properties: {
							switch: configAriaSchema
						},
						additionalProperties: false
					},
					configAriaSchema
				]
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the checked state of the toggle is changed.
		 * Payload contains the value `true` when checked and `false` when unchecked.
		 * Set the `manage-checked` property to override the default behavior and
		 * handle this action manually.
		 * @type {{value: boolean}}
		 * @uib.description Dispatched when the checked state of the toggle is changed
		 */
		'NOW_TOGGLE#CHECKED_SET': {
			schema: {
				type: 'object',
				properties: {
					value: {type: 'boolean'}
				}
			}
		}
	},
	eventHandlers: [
		{
			events: ['keydown'],
			effect: ({host, action}) => {
				const {key} = action.payload.event;
				if (key === 'Enter') {
					host.shadowRoot.querySelector('input').click();
				}
			}
		}
	],
	behaviors: [{behavior: focusBehavior}],
	styles,
	view
});
